<template>
  <div class="project-resume">
    <div class="row">
      <div :class="titleBarLeftClasses" class="col-xs-12">
        <div class="name">
          {{ name }}
        </div>
        <p v-for="info in infos">
          {{ info.text }}
        </p>
      </div>
      <div class="link-container" :class="titleBarRightClasses">
        <md-button class="md-primary" v-if="quickLink"
                   :href="quickLink.href" :target="'_blank'">
                   {{ quickLink.text }}
        </md-button>
      </div>
    </div>
    <p class="resume" v-html="mdLinkToAlink(resume, {target: '_blank'})">
    </p>
    <slot></slot>
  </div>
</template>

<style lang="scss" scoped>
.project-resume{
  .name{
    font-weight: bold;
    text-align: left;
  }

  p{
    margin: 0;
  }

  .resume{
    text-align: justify;
    text-indent: 2em;
    margin-left: 2em;
    margin-top: 1em;
    margin-bottom: 1em;
  }
  .link-container{
    text-align: right;
    padding-right: 0px;
  }
}
</style>

<script>
import LinkMixin from './LinkMixin';

export default{
  name: 'project-resume',
  mixins: [LinkMixin],
  props: {
    name: {
      type: String,
      default: 'Project name',
    },
    infos: {
      type: Array,
      default: [],
    },
    resume: {
      type: String,
      default: '',
    },
    quickLink: {
      type: Object,
      /*default: {
        href: '',
        text: 'Show',
      },*/
    },
  },
  data: function(){
    return {};
  },
  computed: {
    titleBarLeftClasses: function(){
      return {
        'col-xs-7': this.quickLink,
      };
    },
    titleBarRightClasses: function(){
      return {
        'col-xs-5': this.quickLink,
      };
    },
  },
};
</script>



